
.address-recommendation__summary {
    padding: 1rem;
    border: solid .0625rem;
    box-shadow: 0 0 transparent;
    border-radius: $input-border-radius;
    transition: border-color $transition-speed-fast $transition-ease-default,
                background-color $transition-speed-fast $transition-ease-default,
                box-shadow $transition-speed-normal $transition-ease-default;

    &:hover {
        cursor: pointer;
        background-color: $color-primary;
        color: $color-white;
        border-color: $color-primary;
    }
}

.address-recommendation__option {
    &:checked + .address-recommendation__summary {
        border-color: $color-primary;
        background-color: $color-primary;
        color: $color-white;
        box-shadow: 0 .25rem .5rem transparentize($color-black, 0.8);
    }

    &:focus:not(:checked) + .address-recommendation__summary {
        background-color: $color-tertiary;
    }
}

@include media-query(small-up) {
    .address-recommendation {
        &:nth-child(n + 3) {
            margin-top: 1.25rem;
        }
    }
}

@include media-query(small) {
    .address-recommendation {
        &:not(:last-child) {
            margin-bottom: 1.25rem;
        }
    }
}