
.checkout-stage {
    &:not(:last-child) {
        margin-bottom: 1.875rem;
    }
}

    .checkout-stage__header {
        margin-bottom: 2.25rem;
    }

    .checkout-stage__header--separator {
        padding-bottom: 2.25rem;
        border-bottom: solid .0625rem $color-grey-3;
    }

    .checkout-stage__subheader {
        margin-bottom: 1.35rem;
    }

    .checkout-steps-header__message {
        margin-top: 1.5rem;
    }

    .checkout-stage__details-section,
    .checkout-stage__details-section--no-spacing {
        position: relative;
    }

    .checkout-stage__details-division {
        margin-top: 3rem;
        margin-bottom: 3rem;
    }

    .checkout-stage__address-selection {
        max-width: 31.25rem;
    }

    .checkout-stage__address-fields {
        margin-top: 1rem;
    }

    .checkout-stage__phone-number-field {
        .form-group {
            margin-bottom: 0;
        }
    }

    .checkout-stage__address-selection--top-spacing {
        margin-top: 2.5rem;
    }

    .checkout-stage__address-actions {
        margin-top: .7rem;
    }

        .checkout-stage__address-action {
           &:not(:last-child) {
                margin-right: 1em;
            }
        }

    .checkout-stage__title {
        margin-top: 1.25rem;
        margin-bottom: 1.35rem;
    }

    .checkout-stage__shipment {
        position: relative;
    }

    .checkout-stage__shipment--multi {
        padding-top: 2rem;
        padding-bottom: 2rem;
        border: solid .0625rem $color-grey-3;

        &:not(:last-child) {
            margin-bottom: 2rem;
        }
    }

    .checkout-multiship__actions {
        margin-top: 2.5rem;
    }

.checkout-stage__shaded-message {
    padding-top: 1.7em;
    padding-bottom: 1.95em;
}

.checkout-stage__body-message {
    margin: 1.75rem 0;
}

.checkout__error-container {
    .feedback-banner {
        margin-bottom: 2rem;
    }
}

@include media-query(small-up) {
    .checkout {
        padding-top: 3rem;
        padding-bottom: 6rem;
    }

        .checkout-steps-header {
            margin-bottom: 2.5rem;
        }

    .checkout-stage__address-fields {
        max-width: 23.625rem;
    }
}

@include media-query(small) {
    .checkout {
        padding-top: 2rem;
        padding-bottom: 4rem;
    }

        .checkout-steps-header {
            margin-bottom: 1.75rem;
        }
}

.checkout-steps-header {
    .checkout-steps_list {
        padding: 0;
        .checkout-steps_list-guest {
            @include media-query(medium) {
                border-bottom: 1px solid $color-grey-4;
                padding-bottom: 1rem;
            }
            @include media-query(medium-up) {
                padding-right: 1rem;
                border-right: 1px solid $color-grey-4;
            }
        }
        .checkout-steps_list-login {            
            @include media-query(medium) {
                padding-top: 1rem;
            }
            @include media-query(medium-up) {
                padding-left: 1rem;
            }
        }
        .heading-type, p {
            font-size: 0.875rem;
            margin-bottom: 1rem;
        }
        .opt-wrapper {
            display: flex;
            flex-wrap: wrap;
            .checkout-register {
                margin-right: 1rem;
            }
            a {
                text-transform: capitalize;
            }
        }
    }
}

.checkout-stage__summary--shipping {
    .checkout-stage__subheader {
        margin-bottom: 0;
        cursor: pointer;
        .checkout-stage__subheader-icon {
            transition: 0.1s all ease-in-out;
            position: relative;
            top: 7px;
        }
        &:not(.address-summary--active) {
            .checkout-stage__subheader-icon {
                transform: rotate(180deg);
            }
        }
    }
    .address-summary {
        margin-top: 1.35rem;
        position: relative;
        button {
            position: absolute;
            top: 0;
            right: 0;
        }
        &.address-summary--active {
            display: none;
        }
    }
}