
.checkout-stage__credit-card-fields {
    display: none;

    .set--new-payment & {
        display: block;
    }
}

.checkout-stage__stored-payments {
    .set--new-payment & {
        display: none;
    }
}

    .checkout-stage__stored-payment-item {
        &:not(:last-child) {
            margin-bottom: 1.25rem;
        }
    }

        .checkout-stage__stored-payment {
            padding: 1.25rem;
            border: solid .0625rem $color-grey-3;
            border-radius: $input-border-radius;

            &:hover, &.set--selected-payment {
                border-color: $color-grey-7;
            }

            &:not(.set--selected-payment) {
                .checkout-stage__stored-payment-error {
                    display: none;
                }
            }
        }

            .checkout-stage__stored-payment-input {
                display: none;

                .set--selected-payment & {
                    display: block;
                }
            }

    .payment-option__item {
        &:not(:last-child) {
            margin-bottom: 2.125rem;
        }
    }

    .payment-option__description {
        margin-top: 1.25rem;
    }

    .payment-options__content {
        margin-top: 2.5rem;

        &:not(.toggle--active) {
            display: none;
        }
    }

@include media-query(small-up) {
    .payment-options__content {
        padding-left: 2.2rem;
    }

    .payment-options__content--AdyenComponent {
        max-width: 25.7rem;
    }
}
