/**
 * State-based selector structure for the checkout stage.
 * Be careful when updating these, as it might unintentionally break checkout.
 */

[data-checkout-component*="steps-header"],
[data-checkout-component*="details"],
[data-checkout-component*="summary"],
[data-checkout-component*="next"],
[data-stage-container],
[data-checkout-component="placeOrder-acknowledgement"] {
    display: none;
}

[data-current-stage="shipping"] {
    [data-stage-container="shipping"],
    [data-checkout-component="shipping-details"],
    [data-checkout-component="shipping-stage-next"],
    [data-checkout-component="steps-header-shipping"] {
        display: block;
    }
}

[data-current-stage="payment"] {
    [data-stage-container],
    [data-checkout-component="shipping-summary"],
    [data-checkout-component="payment-details"],
    [data-checkout-component="payment-stage-next"],
    [data-checkout-component="steps-header-payment"] {
        display: block;
    }
}


[data-current-stage="placeOrder"],
[data-current-stage="submitted"] {
    [data-stage-container],
    [data-checkout-component*="summary"],
    [data-checkout-component="placeOrder-stage-next"],
    [data-checkout-component="steps-header-placeOrder"],
    [data-checkout-component="placeOrder-acknowledgement"] {
        display: block;
    }
}

[data-checkout-component="address-fields"] {
    display: none;
}

/*------------------------------------*\
  #ADDRESS-MODES
\*------------------------------------*/

[data-checkout-component="shipping-pickup-section"] {
    display: none;
}

[data-address-mode="new"],
[data-address-mode="details"] {
    [data-checkout-component="address-fields"] {
        display: block;
    }

    [data-checkout-component="address-actions"] {
        display: none;
    }
}

[data-pickup-view] {
    [data-checkout-component="shipping-address-section"],
    [data-checkout-component="shipping-gift-section"] {
        display: none;
    }

    [data-checkout-component="shipping-pickup-section"] {
        display: block;
    }

    //Maybe this should be moved somewhere better
    .store-locator__actions {
        display: none;
    }
}

/*------------------------------------*\
  #SHIPMENT-VIEWS
\*------------------------------------*/

[data-checkout-component="multi-shipment-list"],
[data-checkout-component="multi-shipment-message"] {
    display: none;
}

[data-shipping-view="multi"] {
    &[data-current-stage="shipping"] {
        [data-checkout-component="product-list"] {
            display: none;
        }
    }

    [data-checkout-component="shipping-summary"] {
        [data-address-summary="shipping-container"] {
            display: none;
        }
    }

    [data-checkout-component="single-shipping-form"] {
        display: none;
    }

    [data-checkout-component="multi-shipment-list"],
    [data-checkout-component="multi-shipment-message"] {
        display: block;
    }

    [data-multiship-view="editable"] {
        [data-address-summary*="main"],
        [data-multiship-action="new"],
        [data-multiship-action="edit"] {
            display: none;
        }
    }

    [data-multiship-view*="summary"] {
        [data-checkout-component="address-container"],
        [data-multiship-actions="review"] {
            display: none;
        }
    }

    [data-multiship-view="summary-edit"] {
        [data-multiship-action="new"] {
            display: none;
        }
    }

    [data-multiship-view="summary-new"] {
        [data-address-summary*="main"],
        [data-multiship-action="edit"] {
            display: none;
        }
    }
}
