
.order-confirmation {
    margin-top: 4.5rem;
    margin-bottom: 4.5rem;
}

    .order-confirmation__header {
        margin-bottom: 2.25rem;
    }

    .order-confirmation__detail {
        padding-top: 3.5rem;
        padding-bottom: 3.5rem;
    }

    .order-confirmation__info {
        margin-bottom: 1.25rem;
    }

    .order-confirmation__register-wraper {
        margin-top: 2.5rem;
    }

    .order-confirmation__details-section {
        &:not(:last-child) {
            margin-bottom: 1.25rem;
            padding-bottom: 1.25rem;
            border-bottom: solid .0625rem $color-grey-3;
        }
    }

    .order-confirmation__actions {
        margin-top: 2rem;
    }

@include media-query(small-up) {
    .order-confirmation {
        min-height: 28rem;
    }
}

@include media-query(small) {
    .order-confirmation__section {
        &:not(:last-child) {
            margin-bottom: 2rem;
        }
    }
}
