
.shipping-method__item {
    &:not(:last-child) {
        margin-bottom: 2.25rem;
    }
}

.tooltip--shipping-address {
    margin: 0;
    position: absolute;
    top: 0;
    right: -40px;
    margin-left: 1rem;
    margin-top: 0.6rem;
    @include media-query('small') {
        right: 0;
        margin-left: 0;
        top: -25px;
    }
    .tooltip__trigger {
        .tooltip__trigger-icon {
            display: none;
        }
        &::before {
            content: '?';
            border: 2px solid #222;
            border-radius: 100%;
            min-width: 18px;
            height: 18px;
            flex-basis: 100%;
            font-size: 13px;
            font-weight: bold;
        }
    }
}
