
// @import 'select2/src/scss/core.scss';

.select2-container {
    // box-sizing: border-box;

    display: inline-block;
    // margin: 0;
    position: relative;
    vertical-align: middle;

    @import "select2/src/scss/single";
    // @import "select2/src/scss/multiple";
}

@import "select2/src/scss/dropdown";

.select2-close-mask {
    border: 0;
    margin: 0;
    padding: 0;
    display: block;
    position: fixed;
    left: 0;
    top: 0;
    min-height: 100%;
    min-width: 100%;
    height: auto;
    width: auto;
    opacity: 0;
    z-index: 99;

    // styles required for IE to work

    background-color: $color-white;
    filter: alpha(opacity=0);
}

.select2-hidden-accessible {
    border: 0 !important;
    clip: rect(0 0 0 0) !important;
    -webkit-clip-path: inset(50%) !important;
    clip-path: inset(50%) !important;
    height: 1px !important;
    overflow: hidden !important;
    padding: 0 !important;
    position: absolute !important;
    width: 1px !important;
    white-space: nowrap !important;
}

@import "select2/src/scss/theme/default/layout";
// @import "select2/src/scss/theme/classic/layout";

.select2-container--default {
    max-width: 100%;
    min-width: 100%;

    .select2-selection--single {
        width: 100%;
        height: 2.1875rem;
        background-color: $color-input-background;
        transition-property: color, border-color;
        transition-duration: $transition-speed-fast;
        transition-timing-function: $transition-ease-in;

        border-radius: $input-border-radius;
        border: none;
        border-bottom: solid .0625rem $color-body-copy;
        background-color: transparent;
        font-weight: $font-weight-light;

        .select2-selection__rendered {
            line-height: 2.1875rem;
            letter-spacing: normal;
            color: inherit;
        }

        .select2-selection__arrow {
            top: 0;
            height: 100%;

            b {
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                margin-top: 0;
                margin-left: 0;
                border: none;
                background-image: url(get-icon(angle-down, $color-body-copy));
                background-size: .85rem .85rem; //Provide height + width separately to fix IE display.
                background-position: calc(100% - .1em) center;
                background-repeat: no-repeat;
            }
        }
    }

    //select2 attaches its own `[dir]` attribute to the `.select2-container` element.
    //This means we need to add extra specificity as it otherwise overrides the direction settings we want.
    &[dir] .select2-selection--single {
        .select2-selection__rendered {
            padding-left: 0;
            padding-right: 1.125rem;
        }

        .select2-selection__arrow {
            right: 0;
            left: auto;
        }
    }

    .select2-dropdown {
        border-color: currentColor;
        border-radius: $input-border-radius;
    }

        .select2-results__options {
            padding: 0;
        }

            .select2-results__option {
                min-height: 1.875rem;
                font-size: $font-size-deci;

                &[aria-selected=true] {
                    background-color: $color-grey-2;
                }
            }

        .select2-results__option--highlighted[aria-selected] {
            background-color: $color-secondary;
        }

    &.select2-container--open {
        .select2-selection--single {
            .select2-selection__arrow {
                transform: scaleY(-1);
            }
        }
    }
}
