@import "~core/pages/checkout/checkoutStages";

/*------------------------------------*\
  #PLACE OVERRIDES HERE
\*------------------------------------*/

/*------------------------------------*\
  #SHIPPING
\*------------------------------------*/

.checkout-stage__shipment-toggle {
    box-shadow: none;
}

    .checkout-stage__shipment-toggle-item {
        flex-direction: column;
        margin-right: 1rem;
        opacity: 1;
        padding: 4rem 1rem;

        &.toggle--active {
            background-color: transparent;
            border: solid .0625rem $color-primary;
            box-shadow: none;

            &::after {
                position: absolute;
                bottom: 1rem;
                width: 30px;
                height: 18px;
                background: url("data:image/svg+xml,%3Csvg viewBox='0 0 20 12' xmlns='http://www.w3.org/2000/svg'%3E%3Cg stroke='none' stroke-width='1' fill='none' fill-rule='evenodd' stroke-linecap='round' stroke-linejoin='round'%3E%3Cg transform='translate(-1.000000, -5.000000)' stroke='%23000000'%3E%3Cpolyline id='lineart' points='20 5 8 17 2 11'%3E%3C/polyline%3E%3C/g%3E%3C/g%3E%3C/svg%3E") no-repeat;
                content: "";
            }
        }
    }

.checkout-stage__store-locator {
    margin-top: 3rem;
}

.store-locator__title {
    margin-bottom: 2rem;
}

.store-details__address {
    font-family: $font-family-serif;
}

.store-details__delivery {
    margin-bottom: 1rem;
}

.shipping-method__delivery {
    margin-top: .5rem;
}

.address-recommendation__title {
    margin-bottom: .5rem;
}

/*------------------------------------*\
  #PAYMENT
\*------------------------------------*/

// Specificity required to override
.address-summary__group {
    .shipping-method__delivery {
        margin-top: 0;
    }
}

// Specificity required to override
.checkout-stage__payment-options {
    .payment-options__content {
        margin-top: 0;
        padding-left: 0;
    }
}

.payment-option__trigger {
    display: none;
}

/*------------------------------------*\
  #ORDER SUMMARY
\*------------------------------------*/

.order-summary__products {
    overflow-x: hidden;
}
