
.address-summary--multi-ship-summary {
    .address-summary__group {
        display: flex;
        flex-wrap: wrap;
    }

    .address-summary__section {
        flex: 1 1 50%;
        max-width: 50%;
        padding-right: 2em;
    }
}

.address-summary--line-item {
    padding: 1rem;
    background-color: $color-grey-1;
    font-size: $font-size-deci;
}

.address-summary__section {
    &:not(:first-child) {
        margin-top: 1.5em;
    }
}

    .address-summary__title {
        margin-bottom: .5em;
        //Intentionally set to em, allows re-using this component better.
        // font-size: 1em;
    }

    .address-summary__details {
        line-height: $font-line-height-kilo;
        overflow-wrap: break-word;
    }

        .address-summary__row-icon {
            width: 2.25rem;
            margin-right: .5rem;
        }

    .address-summary__actions {
        margin-top: 2rem;
    }


@include media-query(small) {
    .address-summary__action-col {
        &:not(:last-child) {
            margin-bottom: 1.5rem;
        }
    }
}
